.Icon-container {
  width: 100%;
  height: fit-content;
  background-color: var(--mint);
  border-radius: 16px 16px 0px 0px;
  display: inline-flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  color: var(--grey);
  padding-top: 32px;
  padding-bottom: 32px;
  box-shadow: 0 0 30px -10px var(--mint);
  background-image: linear-gradient(to right, var(--bluegreen), var(--mint));
}

.logoicon {
  color: black;
  margin-left: 2%;
  margin-right: 2%;
}

.logoicon:hover {
  opacity: 0.75;
}
