@import url(https://fonts.googleapis.com/css2?family=Inter:wght@200;400;600&display=swap);
body {
  font-family: "Inter", "sans-serif";
  font-weight: 200;
  padding: 0 0 0 0;
  margin: 0 0 0 0;
  min-height: 100%;
}

:root {
  --offwhite: #d7fdec;
  --grey: #938ba1;
  --mint: #a9fbd7;
  --bluegreen: #b2e4db;
  --greyblue: #b0c6ce;
}

.u-textCenter {
  text-align: center;
}

.name-header {
  font-size: 48px;
  font-weight: 400;
}

.u-auto {
  margin-left: auto;
  margin-right: auto;
}

.big-text {
  font-size: 100px;
  font-weight: 300;
}

.smaller-text {
  font-size: 40px;
  font-weight: 400;
}

.return-home:hover {
  opacity: 0.75;
  cursor: pointer;
  transform: scale(1.2);
  transition: 0.7s ease-in-out;
}

.u-bold {
  font-weight: 400;
}

.Icon-container {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: var(--mint);
  border-radius: 16px 16px 0px 0px;
  display: inline-flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  color: var(--grey);
  padding-top: 32px;
  padding-bottom: 32px;
  box-shadow: 0 0 30px -10px var(--mint);
  background-image: linear-gradient(to right, var(--bluegreen), var(--mint));
}

.logoicon {
  color: black;
  margin-left: 2%;
  margin-right: 2%;
}

.logoicon:hover {
  opacity: 0.75;
}

.About-container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: 2%;
}

.About-pfp {
  border-radius: 50%;
  max-width: 278px;
  height: 278px;
}

.bio-text {
  font-size: 16px;
}

.bio-text {
  font-size: 28px;
}

.bio-routes {
  text-decoration: underline;
  cursor: pointer;
  color: black;
}

.bio-routes:hover {
  color: #0269a4;
}

.homepage-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.top-bar {
  background-image: linear-gradient(to right, var(--bluegreen), var(--mint));
}

